import React, { useState } from 'react';
import { AppBar, Tabs, Tab, makeStyles } from '@material-ui/core';
// import SwipeableViews from 'react-swipeable-views';

import { projects } from '../../data/projects';
import TabPanel from '../../components/TabPanel';
import Lawyer from './Lawyer';
import Quiz from './Quiz';
import Store from './Store';
import OnlineShop from './OnlineShop';

function allyProps(index) {
  return {
    id: `full-width-tab-${index}`,
    'aria-controls': `full-width-tabpanel-${index}`,
  };
}

const useStyles = makeStyles(theme => ({
  root: {
    maxWidth: 1000,
    position: 'relative',
    '& .MuiAppBar-positionFixed': {
      position: "static",
    },
    '& .MuiTabs-indicator': {
      background: theme.palette.success.main,
    },
    '& .MuiTab-wrapper': {
      fontWeight: 'bold',
    },
  },
}));

export default function Work() {
  const classes = useStyles();
  const [value, setValue] = useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  // const handleChangeIndex = index => {
  //   setValue(index);
  // };

  const onSHOP = (<p><small>on</small>Shop</p>);

  return (
    <div className={classes.root}>
      <AppBar color="inherit">
        <Tabs
          value={value}
          onChange={handleChange}
          textColor="primary"
          variant="fullWidth"
          aria-label="full width tabs example"
        >
          <Tab label={onSHOP} {...allyProps(0)}/>
          <Tab label="Store" {...allyProps(1)} />
          <Tab label="Quiz" {...allyProps(2)} />
          <Tab label="Lawyer" {...allyProps(3)} />
        </Tabs>
      </AppBar>

      {/* <SwipeableViews */}
      <div>
        <TabPanel value={value} index={0} className="alidein">
          <OnlineShop onlineShop={projects.onlineShop} />
        </TabPanel>

        <TabPanel value={value} index={1} className="slidein">
          <Store store={projects.store} />
        </TabPanel>

        <TabPanel value={value} index={2} className="slidein">
          <Quiz quiz={projects.quiz} />
        </TabPanel>

        <TabPanel value={value} index={3} className="slidein">
          <Lawyer lawyer={projects.lawyer} />
        </TabPanel>        
      </div>
      {/* </SwipeableViews> */}
      
    </div>
  );
}