import React from 'react';
import PropTypes from 'prop-types';
import { Link } from '@material-ui/core';

import ProjectCard from '../../../components/ProjectCard';
import image from './onShop.jpg';

export default function OnlineShop({ onlineShop }) {
  const linkSource = (
    <div>
      <Link
        href='https://github.com/webdevelops/practice/tree/master/angular/Shop'
        target="_blank"
        rel="noopener noreferrer"
      >
        Git - (source here)
      </Link>

      <div>
        <Link
          href="https://online-shop-faba7.firebaseapp.com/admin/login"
          target="_blank"
          rel="noopener noreferrer"
        >
          go to admin panel
      </Link>
      </div>
    </div>
  );

  return (
    <div>
      <ProjectCard
        title={onlineShop.title}
        description={onlineShop.description}
        url={onlineShop.url}
        image={image}
        techList={onlineShop.techList}
        linkSource={linkSource}
      />
    </div>
  )
}

OnlineShop.propTypes = {
  onlineShop: PropTypes.object
};